<template>
  <div class="login-wrap">

    <r-setting v-if="showType===0" @change="changeShow"></r-setting>
    <r-login v-else-if="showType===1" @change="changeShow"></r-login>
    <r-register v-else-if="showType===2" @change="changeShow"></r-register>

    <van-dialog v-model="dialogShow" title="服务协议和隐私政策" show-confirm-button confirm-button-text="同意"
      show-cancel-button cancel-button-text="暂不使用" @confirm="agree" @cancel="cancel">
      <div class="dialog_message">
        <div>
          请你务必审慎阅读、充分理解“服务协议”和“隐私政策”各条款，包括但不限于：为了向你提供即时通讯、内容分享等服务，我们需要收集你的设备信息、操作日志等个人信息。你可以在“设置”中查看、变更、删除个人信息并管理你的授权。
        </div>
        <div>
          你可阅读
          <span class="dialog_operate" @click="showAgreement">《服务协议》</span>
          和
          <span class="dialog_operate" @click="showPolicy">《隐私政策》</span>
          了解详细信息。如你同意，请点击“同意”开始接受我们的服务。
        </div>
      </div>
    </van-dialog>

    <div v-show="showType!==0" class="foot_remark">
      <div class="foot_remark_content">
        <span class="dialog_operate" @click="showAgreement">《服务协议》</span>
        |
        <span class="dialog_operate" @click="showPolicy">《隐私政策》</span>
      </div>
    </div>
  </div>
</template>

<script>
import RLogin from "./components/R-Login";
import RRegister from "./components/R-Register";
import RSetting from "./components/R-Setting";
import { Dialog } from "vant";
import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  name: "login",
  components: {
    RLogin,
    RRegister,
    RSetting,
    [Dialog.Component.name]: Dialog.Component
  },
  beforeCreate() {
    this.$store.dispatch("logout");
  },
  created() {},
  data() {
    return {
      showType: 1, // 0设置，1登录，2注册
      dialogShow: false,
      agreementVisible: false,
      policyVisible: false
    };
  },
  mounted() {
    if (!this.$store.getters.agreeUse) {
      this.dialogShow = true;
    }
  },
  methods: {
    changeShow(val) {
      this.showType = val;
    },
    agree() {
      this.$store.commit("SET_AGREEUSE", 1);
    },
    cancel() {
      sendToAPP(FuncType.exitAPP, "");
    },
    showAgreement() {
      this.$router.push("/agreement");
    },
    showPolicy() {
      this.$router.push("/policy");
    }
  }
};
</script>

<style lang="less">
.login-wrap {
  width: 100%;
  height: 100%;
  background: url("./../../assets/bg_login.png");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  position: relative;
  .login_setting {
    position: absolute;
    right: 2vw;
    top: 2vw;
    font-size: 1.85rem;
  }

  .logo {
    width: 28vw;
    height: 10rem;
    background: url("./../../assets/logo.png") center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 9vw 36vw;
  }
  .loginForm {
    // width: 100%;
    margin: 0 15vw;
    text-align: left;
    .formItem {
      width: 100%;
      margin-bottom: 2rem;
      .iconfont {
        font-size: 1.7rem;
        margin-right: 1vw;
        color: #999;
      }
      input {
        width: 57vw;
        font-size: 1.5rem;
        color: #999;
        padding-left: 0.5rem;
        border: none;
        border-bottom: 1px solid rgba(153, 153, 153, 1);
        box-sizing: border-box;
      }
    }
  }
  .footer {
    margin-top: 2vw;
    button {
      min-width: 66vw;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      border: 1px solid #00cbab;
    }

    .footer_operate {
      padding: 1.75rem 17vw;
      display: flex;
      justify-content: flex-end;
      color: #409eff;
      font-size: 1.25rem;
    }
  }

  .dialog_message {
    padding: 1rem 2rem;
    text-align: left;
  }
  .dialog_operate {
    color: #409eff;
  }
  // .foot_remark {
  //   position: absolute;
  //   bottom: 1rem;
  //   left: 0;
  //   width: 100%;
  //   .foot_remark_content {
  //     text-align: center;
  //   }
  // }
}
</style>



